"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { createClient } from "@/lib/supabase/client"

interface Profile {
  id: string
  username: string
  display_name: string
  bio: string | null
  avatar_url: string | null
}

interface ProfileHeaderProps {
  profile: Profile
  isOwnProfile: boolean
  isFollowing: boolean
  followersCount: number
  followingCount: number
  postsCount: number
}

export function ProfileHeader({
  profile,
  isOwnProfile,
  isFollowing: initialIsFollowing,
  followersCount: initialFollowersCount,
  followingCount,
  postsCount,
}: ProfileHeaderProps) {
  const router = useRouter()
  const [isFollowing, setIsFollowing] = useState(initialIsFollowing)
  const [followersCount, setFollowersCount] = useState(initialFollowersCount)
  const [loading, setLoading] = useState(false)

  const handleFollow = async () => {
    setLoading(true)
    const supabase = createClient()

    const {
      data: { user },
    } = await supabase.auth.getUser()

    if (!user) {
      router.push("/login")
      return
    }

    if (isFollowing) {
      await supabase.from("follows").delete().eq("follower_id", user.id).eq("following_id", profile.id)
      setIsFollowing(false)
      setFollowersCount((prev) => prev - 1)
    } else {
      await supabase.from("follows").insert({ follower_id: user.id, following_id: profile.id })
      setIsFollowing(true)
      setFollowersCount((prev) => prev + 1)
    }

    setLoading(false)
    router.refresh()
  }

  return (
    <div className="border-b bg-card">
      <div className="mx-auto max-w-2xl px-4 py-8">
        <div className="flex items-start gap-6">
          <Avatar className="h-24 w-24">
            <AvatarImage src={profile.avatar_url || undefined} alt={profile.display_name} />
            <AvatarFallback className="text-2xl">{profile.display_name[0].toUpperCase()}</AvatarFallback>
          </Avatar>

          <div className="flex-1">
            <div className="flex items-center justify-between">
              <div>
                <h1 className="text-2xl font-bold">{profile.display_name}</h1>
                <p className="text-muted-foreground">@{profile.username}</p>
              </div>

              {isOwnProfile ? (
                <Button variant="outline" asChild>
                  <Link href="/profile/edit">Edit Profile</Link>
                </Button>
              ) : (
                <Button onClick={handleFollow} disabled={loading} variant={isFollowing ? "outline" : "default"}>
                  {loading ? "Loading..." : isFollowing ? "Unfollow" : "Follow"}
                </Button>
              )}
            </div>

            {profile.bio && <p className="mt-4 text-balance">{profile.bio}</p>}

            <div className="mt-4 flex gap-6 text-sm">
              <div>
                <span className="font-semibold">{postsCount}</span> <span className="text-muted-foreground">posts</span>
              </div>
              <div>
                <span className="font-semibold">{followersCount}</span>{" "}
                <span className="text-muted-foreground">followers</span>
              </div>
              <div>
                <span className="font-semibold">{followingCount}</span>{" "}
                <span className="text-muted-foreground">following</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
